import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
    width: 100%;
    height: 120px;
    box-shadow: 0 -20px 60px 30px;
    background: #040406;
`

const BlackShawDowBlock = () => {
    return <Wrapper/>
}

export default BlackShawDowBlock;